"use client";

import { useState } from "react";
import Image from "next/image";
import Link from "next/link";
import { Search, Package, ChevronRight, ShoppingBag, Plus, CheckCircle, MessageCircle, X, Menu } from "lucide-react";
import { Product, Store } from "@/types";
import Hero from "./Hero"; // Reuse the Hero we made

interface LandingClientProps {
  products: Product[];
  stores: Store[];
}

type CartItem = { product: Product; store: Store; qty: number };

export default function LandingClient({ products, stores }: LandingClientProps) {
  const [view, setView] = useState<'products' | 'vendors'>('products');
  const [search, setSearch] = useState("");
  const [cart, setCart] = useState<CartItem[]>([]);
  const [isCartOpen, setIsCartOpen] = useState(false);
  const [isMenuOpen, setIsMenuOpen] = useState(false); // Navbar Mobile Menu State

  // --- FILTER LOGIC ---
  const filteredProducts = products.filter(p => p.name.toLowerCase().includes(search.toLowerCase()));
  const filteredStores = stores.filter(s => s.name.toLowerCase().includes(search.toLowerCase()));

  // --- CART LOGIC ---
  const addToCart = (product: Product) => {
    const productStore = stores.find(s => s.id === product.store_id);
    if (!productStore) return;

    setCart(prev => {
      const existing = prev.find(item => item.product.id === product.id);
      if (existing) {
        return prev.map(item => item.product.id === product.id ? { ...item, qty: item.qty + 1 } : item);
      }
      return [...prev, { product, store: productStore, qty: 1 }];
    });
    setIsCartOpen(true);
  };

  const removeFromCart = (productId: string) => {
    setCart(prev => prev.filter(item => item.product.id !== productId));
  };

  // Group Cart by Vendor
  const cartByVendor = cart.reduce((acc, item) => {
    const storeId = item.store.id;
    if (!acc[storeId]) acc[storeId] = { store: item.store, items: [] };
    acc[storeId].items.push(item);
    return acc;
  }, {} as Record<string, { store: Store, items: CartItem[] }>);

  return (
    <div className="min-h-screen bg-white font-sans selection:bg-emerald-100">
      
      {/* --- NAVBAR (Integrated here to share state) --- */}
      <nav className="border-b border-gray-100 sticky top-0 bg-white/90 backdrop-blur-md z-40">
        <div className="max-w-7xl mx-auto px-4 h-16 flex items-center justify-between">
          <Link href="/" className="flex items-center gap-2 font-extrabold text-xl tracking-tighter text-gray-900">
            <div className="bg-gray-900 text-white p-1.5 rounded-lg"><ShoppingBag size={18} /></div>
            StoreLink
          </Link>

          {/* Desktop Links */}
          <div className="hidden md:flex items-center gap-6">
             <Link href="/login" className="text-sm font-bold text-gray-600 hover:text-gray-900">Log In</Link>
             <Link href="/onboarding" className="px-5 py-2 bg-gray-900 text-white rounded-xl text-sm font-bold hover:bg-gray-800 transition">
               Start Selling
             </Link>
             {cart.length > 0 && (
               <button onClick={() => setIsCartOpen(true)} className="relative p-2 bg-gray-100 rounded-full hover:bg-gray-200 transition">
                 <ShoppingBag size={20} />
                 <span className="absolute -top-1 -right-1 bg-red-500 text-white text-[10px] font-bold h-4 w-4 flex items-center justify-center rounded-full">
                   {cart.length}
                 </span>
               </button>
             )}
          </div>

          {/* Mobile Toggle */}
          <div className="flex items-center gap-4 md:hidden">
            {cart.length > 0 && (
               <button onClick={() => setIsCartOpen(true)} className="relative p-2 bg-gray-50 rounded-full">
                 <ShoppingBag size={20} />
                 <span className="absolute -top-1 -right-1 bg-red-500 text-white text-[10px] font-bold h-4 w-4 flex items-center justify-center rounded-full">
                   {cart.length}
                 </span>
               </button>
             )}
            <button onClick={() => setIsMenuOpen(!isMenuOpen)}>
              {isMenuOpen ? <X size={24} /> : <Menu size={24} />}
            </button>
          </div>
        </div>

        {/* Mobile Menu */}
        {isMenuOpen && (
          <div className="md:hidden border-t border-gray-100 bg-white p-4 flex flex-col gap-4 animate-in slide-in-from-top-2">
             <Link href="/login" className="text-sm font-bold text-gray-600">Log In</Link>
             <Link href="/onboarding" className="px-5 py-3 bg-gray-900 text-white rounded-xl text-sm font-bold text-center">
               Start Selling
             </Link>
          </div>
        )}
      </nav>

      <Hero />

      {/* --- MARKETPLACE SECTION --- */}
      <section id="marketplace" className="py-12 px-4 bg-gray-50 min-h-screen">
        <div className="max-w-6xl mx-auto mb-8">
          
          {/* Controls */}
          <div className="flex flex-col md:flex-row gap-4 justify-between items-center mb-6">
            <div className="flex bg-white p-1 rounded-xl border border-gray-200 shadow-sm">
              <button 
                onClick={() => setView('products')} 
                className={`px-6 py-2 rounded-lg text-sm font-bold transition ${view === 'products' ? 'bg-gray-900 text-white' : 'text-gray-500 hover:text-gray-900'}`}
              >
                Products
              </button>
              <button 
                onClick={() => setView('vendors')} 
                className={`px-6 py-2 rounded-lg text-sm font-bold transition ${view === 'vendors' ? 'bg-gray-900 text-white' : 'text-gray-500 hover:text-gray-900'}`}
              >
                Vendors
              </button>
            </div>
            
            <div className="relative w-full md:w-96">
              <Search className="absolute left-3 top-3 text-gray-400 w-5 h-5" />
              <input 
                placeholder={`Search ${view}...`} 
                className="w-full pl-10 pr-4 py-3 bg-white border border-gray-200 rounded-xl focus:ring-2 focus:ring-gray-900 outline-none shadow-sm"
                value={search}
                onChange={(e) => setSearch(e.target.value)}
              />
            </div>
          </div>

          {/* Grid */}
          {view === 'products' ? (
            <div className="grid grid-cols-2 md:grid-cols-4 gap-4">
              {filteredProducts.map(product => (
                <div key={product.id} className="bg-white p-2 rounded-2xl border border-gray-100 shadow-sm hover:shadow-md transition group">
                  <div className="aspect-square bg-gray-100 rounded-xl mb-3 relative overflow-hidden">
                    {product.image_urls?.[0] ? (
                      <Image src={product.image_urls[0]} alt="" fill className="object-cover" />
                    ) : (
                      <div className="w-full h-full flex items-center justify-center text-gray-300"><Package /></div>
                    )}
                    <button onClick={() => addToCart(product)} className="absolute bottom-2 right-2 bg-white p-2 rounded-full shadow-lg hover:bg-gray-900 hover:text-white transition">
                      <Plus size={16} />
                    </button>
                  </div>
                  <div className="px-1">
                    <h3 className="font-bold text-gray-900 text-sm truncate">{product.name}</h3>
                    <p className="text-emerald-600 font-bold text-sm mt-1">â‚¦{product.price.toLocaleString()}</p>
                  </div>
                </div>
              ))}
            </div>
          ) : (
            <div className="grid grid-cols-1 md:grid-cols-3 gap-4">
              {filteredStores.map(store => (
                <a href={`/${store.slug}`} key={store.id} className="bg-white p-4 rounded-2xl border border-gray-100 shadow-sm hover:shadow-md transition flex items-center gap-4">
                  <div className="w-16 h-16 bg-gray-100 rounded-full overflow-hidden relative border border-gray-100">
                    {store.logo_url ? <Image src={store.logo_url} alt="" fill className="object-cover" /> : null}
                  </div>
                  <div>
                     <h3 className="font-bold text-gray-900 flex items-center gap-1">
                       {store.name} 
                       {store.subscription_plan === 'premium' && <CheckCircle size={14} className="text-blue-500" />}
                     </h3>
                     <p className="text-xs text-gray-500 mt-1">{store.location}</p>
                     <div className="text-xs text-emerald-600 font-bold mt-1 flex items-center gap-1">
                       Visit Store <ChevronRight size={12} />
                     </div>
                  </div>
                </a>
              ))}
            </div>
          )}
        </div>
      </section>

      {/* --- CART SIDEBAR --- */}
      {isCartOpen && (
        <div className="fixed inset-0 z-50 bg-black/50 flex justify-end">
          <div className="w-full max-w-md bg-white h-full shadow-2xl p-6 overflow-y-auto animate-in slide-in-from-right">
            <div className="flex justify-between items-center mb-6">
              <h2 className="text-xl font-bold flex items-center gap-2"><ShoppingBag /> Your Cart</h2>
              <button onClick={() => setIsCartOpen(false)} className="p-2 bg-gray-100 rounded-full"><X size={20}/></button>
            </div>

            {Object.keys(cartByVendor).length === 0 ? (
               <div className="text-center py-10">
                 <ShoppingBag className="w-12 h-12 text-gray-200 mx-auto mb-3" />
                 <p className="text-gray-500">Your cart is empty.</p>
               </div>
            ) : (
               Object.values(cartByVendor).map(({ store, items }) => {
                 const storeTotal = items.reduce((sum, item) => sum + (item.product.price * item.qty), 0);
                 const vendorNumber = store.whatsapp_number.replace(/\D/g, '').replace(/^0/, '234');
                 const msg = `*New Order via StoreLink Marketplace* ðŸ›ï¸\n\n${items.map(i => `- ${i.qty}x ${i.product.name}`).join('\n')}\n\n*Total: â‚¦${storeTotal.toLocaleString()}*`;
                 const whatsappLink = `https://wa.me/${vendorNumber}?text=${encodeURIComponent(msg)}`;

                 return (
                   <div key={store.id} className="mb-6 border border-gray-100 rounded-2xl p-4 bg-gray-50">
                     <h3 className="font-bold text-gray-900 border-b border-gray-200 pb-2 mb-3 flex justify-between">
                       {store.name}
                       <span className="text-emerald-600">â‚¦{storeTotal.toLocaleString()}</span>
                     </h3>
                     
                     {items.map(item => (
                       <div key={item.product.id} className="flex justify-between text-sm mb-2">
                         <span className="text-gray-600">{item.qty}x {item.product.name}</span>
                         <button onClick={() => removeFromCart(item.product.id)} className="text-red-400 hover:bg-red-50 p-1 rounded"><X size={14}/></button>
                       </div>
                     ))}

                     <a href={whatsappLink} target="_blank" className="mt-4 flex items-center justify-center gap-2 w-full bg-green-600 text-white py-3 rounded-xl font-bold text-sm hover:bg-green-700 transition">
                       <MessageCircle size={18} /> Checkout with {store.name}
                     </a>
                   </div>
                 );
               })
            )}
          </div>
        </div>
      )}

      {/* Floating Cart Button */}
      {cart.length > 0 && !isCartOpen && (
        <button onClick={() => setIsCartOpen(true)} className="fixed bottom-6 right-6 bg-gray-900 text-white p-4 rounded-full shadow-2xl z-40 hover:scale-105 transition">
           <ShoppingBag size={24} />
           <span className="absolute -top-1 -right-1 bg-red-500 w-6 h-6 rounded-full flex items-center justify-center text-xs font-bold border-2 border-white">{cart.length}</span>
        </button>
      )}

      {/* Footer */}
      <footer className="py-12 border-t border-gray-100 text-center bg-white">
         <p className="text-gray-400 text-sm">Â© 2025 StoreLink Inc. The African Marketplace.</p>
      </footer>

    </div>
  );
}