import Link from "next/link";
import { ArrowRight, ShoppingBag } from "lucide-react";

export default function Hero() {
  return (
    <header className="pt-20 pb-24 px-4 text-center bg-white overflow-hidden relative">
      <div className="max-w-4xl mx-auto relative z-10">
        <div className="inline-flex items-center gap-2 px-3 py-1 bg-gray-900 text-white rounded-full font-bold text-xs mb-8">
           <span className="w-2 h-2 bg-green-400 rounded-full animate-pulse"></span>
           The #1 WhatsApp Store Builder
        </div>
        
        <h1 className="text-5xl md:text-7xl font-extrabold text-gray-900 tracking-tight mb-8 leading-[1.1]">
          Your online store, <br className="hidden md:block" />
          <span className="text-transparent bg-clip-text bg-gradient-to-r from-emerald-600 to-teal-500">ready in seconds.</span>
        </h1>
        
        <p className="text-xl text-gray-500 mb-10 max-w-xl mx-auto leading-relaxed">
          Stop sending blurry photos. Create a professional catalog, share one link, and receive orders directly on WhatsApp.
        </p>
        
        <div className="flex flex-col sm:flex-row justify-center gap-4">
          <Link href="/onboarding" className="px-8 py-4 bg-gray-900 text-white text-lg font-bold rounded-2xl shadow-xl hover:bg-gray-800 transition flex items-center justify-center gap-2">
             Create My Store <ArrowRight size={20} />
          </Link>
          <a href="#marketplace" className="px-8 py-4 bg-white text-gray-900 border border-gray-200 text-lg font-bold rounded-2xl hover:bg-gray-50 transition flex items-center justify-center gap-2">
             <ShoppingBag size={20} /> Go Shopping
          </a>
        </div>
      </div>
    </header>
  );
}